nav{
    padding: 0 1rem;
}
.content{
    min-height: calc(100vh - 22.5px - 64px);
    padding: 1.5rem 0;
}

.goods{
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 1rem;
}

.cart-quantity{

}

.cart{
    position: fixed;
    bottom: 2rem;
    right: 2rem;
    cursor: pointer;
    z-index: 5;
    padding: 1rem;
}
@media(min-width : 767px){
    .cart{
        top: 5rem;
        bottom: unset;
    }
}

.basket-list{
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    background-color: white;
    width: 50rem;
    max-width: 95%;
    animation: show 500ms ease-in-out;
}

.basket-close{
    cursor: pointer;
    position: absolute;
    top: 0.5rem;
    right: 0.5rem;
    color: white;
}

.basket-delete{
    cursor: pointer;
}

@keyframes show {
    from{
        top: 70%;
        opacity: 0;
    }
    to{
        top: 50%;
        opacity: 1;
    }
}